.skills{height:650px}.skills-container{width:90%;height:100%;margin:0 auto;padding:40px 20px;margin-top:40px;transition:all .2s ease-in;border-radius:10px;display:flex;flex-direction:column;justify-content:space-between}.skills-container__title{text-align:center;font-size:32px}@media(max-width: 768px){.skills-container__title{font-size:18px}}.skills-container__list{width:80%;margin:0 auto;display:grid;grid-template-columns:repeat(3, 1fr);justify-items:flex-start;gap:10px;float:right;margin-right:20px}@media(max-width: 768px){.skills-container__list{width:100%;grid-template-columns:repeat(2, 1fr);margin-right:0px;padding:15px 0px 0px 45px}}.skills-container__list li{position:relative;font-size:20px}@media(max-width: 768px){.skills-container__list li{font-size:14px}}.skills-container__list li::before{content:"▹";position:absolute;left:-20px;color:#2dd4bf}.skills-container__logos{margin:50px 0px}.skills-container__logos_wrapper{display:flex;justify-content:center;align-items:center}@media(max-width: 768px){.skills-container__logos_wrapper{flex-wrap:wrap;gap:12px}}.skills-container__logos_wrapper li{display:flex;justify-content:center;align-items:center;margin:0px 12px}.skills-container__logos_wrapper li i{font-size:55px}@media(max-width: 768px){.skills-container__logos_wrapper li i{font-size:30px}}